import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import BankReconcileCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/financial/bankReconcile/actions";
import { Box } from "../../../App/app.style";
import { Tabs } from "antd";


const TabPane = Tabs.TabPane;

const redirectTo = "/dashboard/financials/bank-reconciles"

class CreateBankReconcile extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createBankReconcile } = this.props;
    
    createBankReconcile();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.bankReconcile.error) {
      notification("error", this.props.bankReconcile.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.bankReconcile.success) {
      const id = this.props.bankReconcile.id_bank_reconcile
      const { nullBankReconcile } = this.props;
      nullBankReconcile();
      this.props.history.replace(redirectTo + "/update/" + id)
    }
  }

  handleShowBankReconcile = (id_cash_bank, start_date, reconcile_date) => {
    const { showBankReconcile } = this.props;
    showBankReconcile(id_cash_bank, start_date, reconcile_date);
  }

  handleSubmit = value => {
    const { storeBankReconcile } = this.props;
    storeBankReconcile(value);
  };

  render() {
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    // console.log(permissions);
    
    // console.log(this.props.bankReconcile);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="bankReconciles.add.title" />}
            style={{ margin:0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <Tabs defaultActiveKey="1">
            <TabPane tab={<IntlMessages id="bankReconciles.tab.detail" />} 
              key="1" 
            >
              <BankReconcileCreateForm
                submit={this.handleSubmit}
                bankReconcile={this.props.bankReconcile}
                handleShowBankReconcile={this.handleShowBankReconcile}
                permissions={permissions}
                history={this.props.history}
                user={user}
              />    
            </TabPane>
            <TabPane tab={<IntlMessages id="bankReconciles.tab.attachment" />} 
              key="2"
              disabled
            >  
            </TabPane>
          </Tabs>
          
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { 
    bankReconcile: state.bankReconcile,
    auth: state.auth
   };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateBankReconcile);
