const actions = {
  getBankReconciles: (page, search, filter) => ({
    type: 'GET_BANK_RECONCILES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createBankReconcile: () => ({
    type: 'CREATE_BANK_RECONCILE_REQUEST'
  }),
  storeBankReconcile: data => ({
    type: 'STORE_BANK_RECONCILE_REQUEST',
    payload: { body: data }
  }),
  editBankReconcile: id_bank_reconcile => ({
    type: 'EDIT_BANK_RECONCILE_REQUEST',
    payload: { id_bank_reconcile: id_bank_reconcile }
  }),
  updateBankReconcile: (id_bank_reconcile, data) => ({
    type: 'UPDATE_BANK_RECONCILE_REQUEST',
    payload: {
      id_bank_reconcile: id_bank_reconcile,
      body: data
    }
  }),
  showBankReconcile: (id_cash_bank, start_date, reconcile_date) => ({
    type: 'SHOW_BANK_RECONCILE_REQUEST',
    payload: { 
      id_cash_bank,
      start_date,
      reconcile_date
    }
  }),
  nullBankReconcile : () => ({
    type: 'NULL_BANK_RECONCILE'
  }),
  // showBankReconcile: id_bank_reconcile => ({
  //   type: 'SHOW_BANK_RECONCILE_REQUEST',
  //   payload: { id_bank_reconcile: id_bank_reconcile }
  // }),
  processBankReconcile: id_bank_reconcile => ({
    type: "PROCESS_BANK_RECONCILE_REQUEST",
    payload: { id_bank_reconcile }
  }),
  cancelBankReconcile: (id_bank_reconcile, data) => ({
    type: "CANCEL_BANK_RECONCILE_REQUEST",
    payload: { 
      id_bank_reconcile,
      body : data
    }
  }),
  uploadDocument: (id_bank_reconcile, data) => ({
    type: "UPLOAD_BANK_RECONCILE_DOCUMENT_REQUEST",
    payload: {
      id_bank_reconcile,
      body : data
    }
  }),
  downloadDocument: id_bank_reconcile_document => ({
    type: "DOWNLOAD_BANK_RECONCILE_DOCUMENT_REQUEST",
    payload: {id_bank_reconcile_document}
  }),
  deleteDocument: id_bank_reconcile_document => ({
    type: "DELETE_BANK_RECONCILE_DOCUMENT_REQUEST",
    payload: { id_bank_reconcile_document }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
