import React, { Component } from "react";

import moment from "moment";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import {
  Form,
  Input,
  Select,
  Col,
  InputNumber,
  DatePicker,
  Empty,
  Divider,
  Modal,
  Icon
} from "antd";
import Scrollbars from "../../../../components/utility/customScrollBar";
import CardWrapper, { TransNo, StatusTag } from "../../../App/app.style";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import NumberFormat from "../../../App/NumberFormat";
import getLanguage from "../../../App/MyLanguange";

const { Option } = Select;
const InputGroup = Input.Group;
// const TextArea = Input.TextArea;
const redirectTo = "/dashboard/financials/bank-reconciles";

class BankReconcileCreateForm extends Component {
  state = {
    selectedRowKeys: []
  };

  componentDidUpdate = prevProps => {
    if (
      prevProps.bankReconcile.show_bank_reconcile !==
        this.props.bankReconcile.show_bank_reconcile &&
      this.props.bankReconcile.show_bank_reconcile !== null
    ) {
      // console.log("Masuk");
      // console.log(this.props.bankReconcile.show_bank_reconcile);
      const { setFieldsValue } = this.props.form;
      if (
        this.props.bankReconcile.show_bank_reconcile.bank_reconcile !== null
      ) {
        this.props.history.replace(
          redirectTo +
            "/update/" +
            this.props.bankReconcile.show_bank_reconcile.bank_reconcile
              .id_bank_reconcile
        );
      } else if (
        this.props.bankReconcile.show_bank_reconcile.last_reconcile !== null
      ) {
        setFieldsValue({
          last_reconcile_date: moment(
            this.props.bankReconcile.show_bank_reconcile.last_reconcile
              .reconcile_date,
            "YYYY-MM-DD"
          ),
          last_reconcile_bank_statement_balance: this.props.bankReconcile
            .show_bank_reconcile.last_reconcile.bank_statement_balance
        });
      }
    }
  };

  handleShowBankReconcile = (name, e) => {
    const { getFieldValue } = this.props.form;
    let id_cash_bank, start_date, reconcile_date;
    if (
      name === "id_cash_bank" &&
      getFieldValue("start_date") &&
      getFieldValue("reconcile_date")
    ) {
      id_cash_bank = e;
      start_date = getFieldValue("start_date").format("YYYY-MM-DD");
      reconcile_date = getFieldValue("reconcile_date").format("YYYY-MM-DD");
    } else if (
      name === "start_date" &&
      getFieldValue("id_cash_bank") &&
      getFieldValue("reconcile_date")
    ) {
      id_cash_bank = getFieldValue("id_cash_bank");
      start_date = e.format("YYYY-MM-DD");
      reconcile_date = getFieldValue("reconcile_date").format("YYYY-MM-DD");
    } else if (
      name === "reconcile_date" &&
      getFieldValue("id_cash_bank") &&
      getFieldValue("start_date")
    ) {
      id_cash_bank = getFieldValue("id_cash_bank");
      start_date = getFieldValue("start_date").format("YYYY-MM-DD");
      reconcile_date = e.format("YYYY-MM-DD");
    }

    if (id_cash_bank && start_date && reconcile_date) {
      this.props.handleShowBankReconcile(
        id_cash_bank,
        start_date,
        reconcile_date
      );
    }
  };

  handleChangeCashBank = e => {
    const { setFieldsValue, getFieldValue } = this.props.form;
    this.props.bankReconcile.cash_banks.map(data => {
      if (data.id_cash_bank === e) {
        setFieldsValue({
          id_currency: data.id_currency
        });
      }
    });
    this.handleShowBankReconcile("id_cash_bank", e);

    setFieldsValue({
      calculated_amount: 0,
      unbalance_amount: getFieldValue("bank_statement_balance")
    });
    this.setState({ selectedRowKeys: [] });
  };

  handleChangeLocation = () => {
    const { setFieldsValue } = this.props.form;
    setFieldsValue({
      id_cash_bank: null,
      id_currency: null
    });
  };

  handleChangeBankStatementBalance = e => {
    const { setFieldsValue, getFieldValue } = this.props.form;
    setFieldsValue({
      unbalance_amount: e - getFieldValue("calculated_amount")
    });
  };

  onSelectRow = selectedRowKeys => {
    const { cash_managements } = this.props.bankReconcile.show_bank_reconcile;
    const { setFieldsValue, getFieldValue } = this.props.form;
    let calculated_amount = 0;
    let bank_statement_balance = getFieldValue("bank_statement_balance")
      ? getFieldValue("bank_statement_balance")
      : 0;

    let x = cash_managements.filter(cash_management =>
      selectedRowKeys.find(
        id_cash_management =>
          cash_management.id_cash_management === id_cash_management
      )
    );

    x.map(object => {
      if (object.cash_flow_type === 1) {
        calculated_amount += parseFloat(object.currency_amount);
      } else if (object.cash_flow_type === 2) {
        calculated_amount -= parseFloat(object.currency_amount);
      }
    });

    setFieldsValue({
      calculated_amount,
      unbalance_amount: bank_statement_balance - calculated_amount
    });
    this.setState({ selectedRowKeys });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        const { getFieldValue } = this.props.form;

        values.reconcile_date = values.reconcile_date.format("YYYY-MM-DD");
        values.cash_managements = this.state.selectedRowKeys;

        if (getFieldValue("unbalance_amount") !== 0) {
          const { messages } = this.props.bankReconcile;
          Modal.confirm({
            title: messages.title,
            content: messages.content,
            icon: <Icon type="info-circle" />,
            okText: messages.ok_button,
            cancelText: messages.cancel_button,
            onOk: () => {
              this.props.submit(values);
            },
            onCancel() {}
          });
        } else {
          this.props.submit(values);
        }
      }
    });
  };

  render() {
    // Definisi kolom2 serta field2 API yang dipakai tabel
    let columns = [
      {
        title: getLanguage({ id: "transactionDate" }),
        dataIndex: "expected_date",
        rowKey: "expected_date",
        width: "10%",
        render: text => (
          <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>
        )
      },
      {
        title: getLanguage({ id: "cashManagements.no" }),
        dataIndex: "cash_flow_no",
        rowKey: "cash_flow_no",
        width: "25%",
        render: text => {
          return (
            <div style={{ display: "flex", height: "20px" }}>
              <TransNo>{text}</TransNo>
            </div>
          );
        }
      },
      {
        title: getLanguage({ id: "transactionType" }),
        dataIndex: "cash_flow_type",
        rowKey: "cash_flow_type",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "primary";
            display = getLanguage({ id: "deposit" });
          } else {
            className = "active";
            display = getLanguage({ id: "withdrawal" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "rate" }),
        dataIndex: "currency_rate",
        rowKey: "currency_rate",
        width: "10%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "amount" }),
        dataIndex: "currency_amount",
        rowKey: "currency_amount",
        width: "10%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "baseAmount" }),
        dataIndex: "cash_flow_amount",
        rowKey: "cash_flow_amount",
        width: "10%",
        align: "right",
        render: text => <NumberFormat value={text} />
      },
      {
        title: getLanguage({ id: "reconciledDate" }),
        dataIndex: "reconciled_date",
        rowKey: "reconciled_date",
        width: "10%",
        render: text => {
          if (text === null) {
            return <span />;
          } else {
            return <span>{moment(text).format("DD/MM/YYYY")}</span>;
          }
        }
      }
    ];
    // console.log(this.props.bankReconcile);
    const { getFieldDecorator, getFieldValue } = this.props.form;
    const { bankReconcile, user } = this.props;
    const { selectedRowKeys } = this.state;
    const { loading } = bankReconcile;
    // const show_bank_reconcile = bankReconcile.show_bank_reconcile
    //   ? bankReconcile.show_bank_reconcile
    //   : [];
    const cash_managements = bankReconcile.show_bank_reconcile
      ? bankReconcile.show_bank_reconcile.cash_managements
      : [];
    //console.log(cash_managements)
    const dataSource = [];

    let permissions = this.props.permissions;

    // const error = bankReconcile.errorField ? bankReconcile.errorField : [];
    // console.log(data);

    cash_managements.map((cash_management, index) => {
      let the_data = {
        ...cash_management,
        key: cash_management.id_cash_management
      };

      return dataSource.push(the_data);
    });

    //console.log(dataSource)

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="bankReconciles.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 15px 0 0" }}
          >
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="startDate" />}
                >
                  {getFieldDecorator("start_date", {
                    // initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY"
                      //disabled={true}
                      allowClear={false}
                      disabledDate={e =>
                        getFieldValue("reconcile_date")
                          ? e.valueOf() > moment()
                          : e.valueOf() > getFieldValue("reconcile_date")
                      }
                      onChange={this.handleShowBankReconcile.bind(
                        this,
                        "start_date"
                      )}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="reconcileDate" />}
                >
                  {getFieldDecorator("reconcile_date", {
                    initialValue: moment(),
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <DatePicker
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY"
                      disabled={
                        !permissions.includes("Can Back Date") ? true : false
                      }
                      allowClear={false}
                      disabledDate={e => e.valueOf() > moment()}
                      onChange={this.handleShowBankReconcile.bind(
                        this,
                        "reconcile_date"
                      )}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.pageHeader" />}
                >
                  {getFieldDecorator("id_location", {
                    initialValue:
                      user && user.default_employee_location
                        ? user.default_employee_location.id_location
                        : null,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.bankReconcile.loading}
                      showSearch
                      optionFilterProp="children"
                      // disabled={used ? true : false}
                      onChange={this.handleChangeLocation}
                      style={{ width: "100%" }}
                    >
                      {bankReconcile.locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {data.location_code + " (" + data.location_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="cashBanks.pageHeader" />}
                >
                  {getFieldDecorator("id_cash_bank", {
                    // initialValue: data.id_cash_bank,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.bankReconcile.loading}
                      showSearch
                      // allowClear={true}
                      optionFilterProp="children"
                      onChange={this.handleChangeCashBank}
                      notFoundContent={
                        getFieldValue("id_location") ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noLocation" />}
                          />
                        )
                      }
                      style={{ width: "100%" }}
                      // disabled={used ? true : false}
                    >
                      {getFieldValue("id_location")
                        ? bankReconcile.cash_banks.map((data, key) =>
                            getFieldValue("id_location") ===
                            data.id_location ? (
                              <Option key={key} value={data.id_cash_bank}>
                                {data.bank_account_number === null
                                  ? data.cash_bank_code
                                  : data.cash_bank_code +
                                    " (" +
                                    data.bank_account_number +
                                    ")"}
                              </Option>
                            ) : null
                          )
                        : null}
                    </Select>
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="currencies.pageHeader" />}
                >
                  {getFieldDecorator("id_currency", {
                    // initialValue: data.id_currency,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Select
                      loading={this.props.bankReconcile.loading}
                      showSearch
                      optionFilterProp="children"
                      disabled={true}
                      style={{ width: "100%" }}
                    >
                      {bankReconcile.currencies.map((data, key) => (
                        <Option key={key} value={data.id_currency}>
                          {data.currency_code + " (" + data.currency_name + ")"}
                        </Option>
                      ))}
                    </Select>
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="bankStatementBalance" />}
                >
                  {getFieldDecorator("bank_statement_balance", {
                    //initialValue: data.bank_statement_balance,
                    //initialValue: 0,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <InputNumber
                      precision={2}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      //min={1}
                      //disabled={used ? true : false}
                      style={{ width: "100%" }}
                      onChange={this.handleChangeBankStatementBalance}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </div>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 0 0 15px" }}
          >
            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="lastReconcileDate" />}
                >
                  {getFieldDecorator("last_reconcile_date", {
                    // initialValue: moment(),
                  })(
                    <DatePicker
                      placeholder={getLanguage({ id: "nothing" })}
                      style={{ width: "100%" }}
                      showTime
                      format="DD MMMM YYYY"
                      disabled={true}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={
                    <IntlMessages id="lastReconcileBankStatementBalance" />
                  }
                >
                  {getFieldDecorator("last_reconcile_bank_statement_balance", {
                    initialValue: 0
                  })(
                    <InputNumber
                      precision={2}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      //min={0}
                      disabled={true}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>

            <InputGroup>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="calculatedAmount" />}
                >
                  {getFieldDecorator("calculated_amount", {
                    // initialValue: data.calculated_amount,
                    initialValue: 0
                  })(
                    <InputNumber
                      precision={2}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      //min={0}
                      disabled={true}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="unbalanceAmount" />}
                >
                  {getFieldDecorator("unbalance_amount", {
                    //initialValue: data.unbalance_amount,
                  })(
                    <InputNumber
                      precision={2}
                      formatter={value =>
                        `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                      }
                      parser={value => value.replace(/\$\s?|(,*)/g, "")}
                      //min={0}
                      disabled={true}
                      style={{ width: "100%" }}
                    />
                  )}
                </Form.Item>
              </Col>
            </InputGroup>
          </div>
        </div>
        <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="bankReconciles.add.subtitle.data" />
        </Divider>

        <span style={{ marginLeft: 8 }}>
          {getLanguage({
            id: "selectedLengthCashManagements",
            values: { length: selectedRowKeys.length }
          })}
        </span>

        <CardWrapper>
          {cash_managements.length === 0 &&
          !this.props.bankReconcile.loading ? (
            <Empty
              description={<IntlMessages id="bankReconciles.table.noData" />}
            />
          ) : (
            <div className="isoInvoiceTable">
              <Scrollbars style={{ width: "100%" }}>
                <TableWrapper
                  // size="small"
                  rowSelection={{
                    selectedRowKeys: selectedRowKeys,
                    onChange: this.onSelectRow
                  }}
                  dataSource={dataSource}
                  columns={columns}
                  loading={loading}
                  className="invoiceListTable"
                  bordered={true}
                  // Set pagination, jika ada page_size mengunakan pagination API
                  // jika tidak ada page_size menggunakan pagination bawaan
                  pagination={
                    true
                    // page_size
                    //   ? {
                    //       current: current_page,
                    //       pageSize: parseInt(page_size),
                    //       total: total,
                    //       onChange: this.handlePageChange.bind(this)
                    //     }
                    //   : true
                  }
                />
              </Scrollbars>
            </div>
          )}
        </CardWrapper>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px" }}
            loading={loading}
            disabled={selectedRowKeys.length > 0 ? false : true}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedBankReconcileCreateForm = Form.create({
  name: "create_bank_reconcile"
})(BankReconcileCreateForm);

export default WrappedBankReconcileCreateForm;
